.uppercase {
    text-transform: uppercase;
}


.bold {
    font-weight: bold !important;
}

.bg-img {
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
}

.white {
    color: $white !important;
}

//font-family 
.barlow {
    font-family: 'Barlow', sans-serif;
}

.title {
    color: $title;
    font-size: rem_calc(46px);
    width: 85%;
    
    @media (max-width: 768px) {
        font-size: rem_calc(30px);
    }
}

.entry-text {
    color: $text;
    font-size: rem_calc(24px);
    line-height: 1.5;
    
    @media (max-width: 768px) {
        font-size: rem_calc(18px);
    }
}

.pink-text {
    color: $pink;
    font-size: rem_calc(150px);
    font-weight: bold;
    
    @media (max-width: 768px) {
        font-size: rem_calc(75.47px);
    }
}

span.dot {
    font-size: rem_calc(50px);
    display: inline-block;
    line-height: .2;
    
    &.news-dot {
        color: #CFD8E1;
    }
}
