
* {
  padding: 0;
  margin: 0;
  outline: none !important;
  box-sizing: border-box;
  line-height: 1.2;
  word-break: break-word;
  -webkit-font-smoothing: antialiased !important;
  -moz-osx-font-smoothing: grayscale !important;
}


*:before,
*:after {
  box-sizing: border-box;
}

span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

ul,
ol {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

a {
  text-decoration: none;
  outline: none;
}

a:focus {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

textarea {
  resize: vertical;
}

html,
body {
    overflow-x: hidden; 
    max-width: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Barlow', sans-serif;
  background-color: $white;
}

.table-striped tbody tr:nth-of-type(odd) {
    background-color: #f4f4f4 !important;
}

.table td, .table th {
    border: 0 !important;
}

.table {
    border: 1px solid #f4f4f4;
}

.container {
    max-width: 1440px;
}
